import React, { Component } from "react";
import { connect } from "react-redux";
import {
  Row,
  Col
} from "reactstrap";

import { Link } from "react-router-dom";

//Import i18n
import { withNamespaces } from "react-i18next";


// Redux Store
import { toggleRightSidebar } from "../../store/actions";

import whitelogo from "../../assets/images/white-logo.png";
class Header extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isSearch: false,
      isSocialPf: false
    };
    this.toggleMenu = this.toggleMenu.bind(this);
    this.toggleRightbar = this.toggleRightbar.bind(this);
    this.toggleFullscreen = this.toggleFullscreen.bind(this);
  }
  /**
   * Toggle sidebar
   */
  toggleMenu() {
    this.props.toggleMenuCallback();
  }

  /**
   * Toggles the sidebar
   */
  toggleRightbar() {
    this.props.toggleRightSidebar();
  }


  toggleFullscreen() {
    if (
      !document.fullscreenElement &&
      /* alternative standard method */ !document.mozFullScreenElement &&
      !document.webkitFullscreenElement
    ) {
      // current working methods
      if (document.documentElement.requestFullscreen) {
        document.documentElement.requestFullscreen();
      } else if (document.documentElement.mozRequestFullScreen) {
        document.documentElement.mozRequestFullScreen();
      } else if (document.documentElement.webkitRequestFullscreen) {
        document.documentElement.webkitRequestFullscreen(
          Element.ALLOW_KEYBOARD_INPUT
        );
      }
    } else {
      if (document.cancelFullScreen) {
        document.cancelFullScreen();
      } else if (document.mozCancelFullScreen) {
        document.mozCancelFullScreen();
      } else if (document.webkitCancelFullScreen) {
        document.webkitCancelFullScreen();
      }
    }
  }

  render() {
    return (
      <React.Fragment>
            <header className="main-bg-color my-header">
                <Row className="navbar-header">
                    <Col xl="6" lg="4" className="my-header-logo d-flex align-items-center">
                      <div className="d-flex align-items-center">
                        <Link to="/dashboard">
                          <img src={whitelogo} alt="" className="my-logo"/>
                        </Link>
                      </div>
                    </Col>
                    <Col xl="6" lg="8" className="text-white  titles-box">
                      <Row>
                        <Col lg="3" className="text-center">
                          <Link className="my-header-text text-center" to="/bot-map">
                              Bot Map
                          </Link>
                        </Col>
                        <Col lg="3" className="text-center">
                          <Link className="my-header-text text-center"  to="/initiate-bots">
                              Initiate a Bot!
                          </Link>
                        </Col>
                        <Col lg="3" className="text-center">
                          <Link className="my-header-text text-center" to="/my-bots">
                              My Bots
                          </Link>
                        </Col>
                        <Col lg="3" className="text-center">
                          <Link className="my-header-text text-center" to="/bot-exchange">
                              Bot Exchange
                          </Link>
                        </Col>
                      </Row>
                    </Col>
                </Row>
                <div className="dropdown1">
                    <button className="dropbtn1"></button>
                    <div className="dropdown-content1">
                        <a href="/bot-map">Bot Map</a>
                        <a href="/initiate-bots">Initiate a Bot!</a>
                        <a href="/my-bots">My Bots</a>
                        <a href="/bot-exchange">Bot Exchange</a>
                    </div>
                </div> 
            </header>
      </React.Fragment>
    );
  }
}

const mapStatetoProps = state => {
  const { layoutType } = state.Layout;
  return { layoutType };
};

export default connect(mapStatetoProps, { toggleRightSidebar })(withNamespaces()(Header));
