import React from "react";
import { Redirect } from "react-router-dom";


// Dashboard
import Dashboard from "../pages/Dashboard/index";

//About this Project
import MyBots from "../pages/Bots"
import InitiateBot from "../pages/Initiate"
import BotMap from "../pages/BotMap";
import Exchange from "../pages/Exchange";


const authProtectedRoutes = [
	// Tables
	{ path: "/dashboard", component: Dashboard },
	
	// this route should be at the end of all other routes
	{ path: "/", exact: true, component: () => <Redirect to="/dashboard" /> }
];

const publicRoutes = [
	{ path: "/my-bots", component: MyBots },
	{ path: "/initiate-bots", component: InitiateBot },
	{ path: "/bot-map", component: BotMap },
	{ path: "/bot-exchange", component: Exchange },



	// Authentication Inner
];

export { authProtectedRoutes, publicRoutes };
