import React, { Component } from "react";
import { Link } from "react-router-dom";
import {
  Row,  Col
} from "reactstrap"; 
import $ from "jquery";
import whitelogo from "./../assets/images/white-logo.png";
                
class Header extends Component {
    constructor(props) {
        super(props);
    }
    componentDidMount() {
        if (window.location.href.indexOf("map") > -1) // This doesn't work, any suggestions?
        {
            $(".h-map").css("cssText", "text-decoration:underline !important;font-weight: 600;");
        }
        if (window.location.href.indexOf("my-bots") > -1) // This doesn't work, any suggestions?
        {
            $(".h-mybots").css("cssText", "text-decoration:underline !important;font-weight: 600;");
        }
        if (window.location.href.indexOf("initiate") > -1) // This doesn't work, any suggestions?
        {
            $(".h-initiate").css("cssText", "text-decoration:underline !important;font-weight: 600;");
        }
        if (window.location.href.indexOf("exchange") > -1) // This doesn't work, any suggestions?
        {
            $(".h-exchange").css("cssText", "text-decoration:underline !important;font-weight: 600;");
        }
    }
    render() {
        return (  
                <header className="main-bg-color my-header">
                    <Row className="navbar-header">
                        <Col xl="6" lg="4" className="my-header-logo">
                            <Link to="/dashboard">
                                <img src={whitelogo} alt="" className="my-logo"/>
                            </Link>
                        </Col>
                        <Col xl="6" lg="8" className="text-white titles-box">
                            <Row>
                                <Col lg="3" className="text-center">
                                    <Link className="my-header-text text-center h-map" to="/bot-map">
                                        Bot Map
                                    </Link>
                                </Col> 
                                <Col lg="3" className="text-center">
                                    <Link className="my-header-text text-center h-initiate"  to="/initiate-bots">
                                        Initiate a Bot!
                                    </Link>
                                </Col>
                                <Col lg="3" className="text-center">
                                    <Link className="my-header-text text-center h-mybots" to="/my-bots">
                                        My Bots
                                    </Link>
                                </Col>
                                <Col lg="3" className="text-center">
                                    <Link className="my-header-text text-center h-exchange" to="/bot-exchange">
                                        Bot Exchange
                                    </Link>
                                </Col>
                            </Row>
                        </Col>
                    </Row>
                    <div className="dropdown1">
                        <button className="dropbtn1"></button>
                        <div className="dropdown-content1">
                            <a href="/bot-map">Bot Map</a>
                            <a href="/initiate-bots">Initiate a Bot!</a>
                            <a href="/my-bots">My Bots</a>
                            <a href="/bot-exchange">Bot Exchange</a>
                        </div>
                    </div> 
                </header>
        );
    }
}

export default Header;